import { Line, mixins } from 'vue-chartjs'
import moment from 'moment'
// eslint-disable-next-line no-unused-vars
import chartjsPluginAnnotation from 'chartjs-plugin-annotation'

export default {
  extends: Line,
  mixins: [mixins.reactiveProp],
  props: ['chartData', 'options', 'diffType'],
  computed: {
    unit () {
      const units = {
        days: 'day', weeks: 'week', years: 'year', hours: 'hour'
      }
      return units[this.diffType]
    }
  },
  watch: {
    chartData (val, oldVal) {
      // console.log('updating chart')
      this.renderChart(this.chartData, {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          labels: {
            fontSize: 16,
            usePointStyle: true
          }
        },
        tooltips: {
          bodyFontSize: 14,
          mode: 'index',
          intersect: true,
          callbacks: {
            title: (tooltipItems, data) => {
              var xLabel = tooltipItems[0].label
              var label = tooltipItems[0].label
              var dt = moment(xLabel)
              if (this.unit === 'week') {
                let lastday = dt.endOf('isoWeek')
                label = `${moment(xLabel).format('ddd D MMM YYYY')} - ${lastday.format('ddd D MMM YYYY')}`
              } else if (this.unit === 'day') {
                label = `${moment(xLabel).format('ddd D MMM YYYY')}`
              } else if (this.unit === 'hour') {
                let lastday = dt.endOf('hour')
                label = `${moment(xLabel).format('lll')} - ${lastday.format('lll')}`
              } else if (this.unit === 'year') {
                let lastday = dt.endOf('year')
                label = `${moment(xLabel).format('D MMM YYYY')} - ${lastday.format('D MMM YYYY')}`
              }
              return label
            },
            label: (tooltipItem, data) => {
              let datasetIndex = tooltipItem.datasetIndex
              let value = `${moment(tooltipItem.label).format('ddd D MMM')}: ${tooltipItem.value}`
              if (datasetIndex === 1 && this.chartData.datasets[datasetIndex] && this.chartData.metaData.compareLabels) {
                value = `${moment(this.chartData.metaData.compareLabels[moment(tooltipItem.label)]).format('ddd D MMM')}: ${tooltipItem.value}`
              }
              return value
            }
          }
        },
        hover: {
          mode: 'nearest',
          intersect: true
        },
        scales: {
          xAxes: [{
            type: 'time',
            scaleLabel: {
              display: true,
              labelString: 'Time',
              fontColor: '#333'

            },
            time: {
              unit: this.unit,
              isoWeekday: true
            }
          }],
          yAxes: [{
            scaleLabel: {
              display: true,
              lablabelStringel: 'Ratings',
              fontColor: '#333'
            }
          }]
        }
      })
    },
    unit (val, oldVal) {
      if (val !== oldVal) {
        this.renderChart(this.chartData, this.options || {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            labels: {
              fontSize: 16,
              usePointStyle: true
            }
          },
          tooltips: {
            bodyFontSize: 14,
            mode: 'index',
            intersect: true
          },
          hover: {
            mode: 'nearest',
            intersect: true
          },
          scales: {
            xAxes: [{
              type: 'time',
              scaleLabel: {
                display: true,
                label: 'Time'
              },
              time: {
                unit: this.unit
              }
            }],
            yAxes: [{
              scaleLabel: {
                display: true,
                label: 'Ratings'
              }
            }]
          }
        })
      }
    }
  },
  mounted () {
    // this.chartData is created in the mixin.
    // If you want to pass options please create a local options object
    // console.log(this)
    this.renderChart(this.chartData, {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        labels: {
          fontSize: 16,
          usePointStyle: true
        }
      },
      tooltips: {
        bodyFontSize: 14,
        mode: 'index',
        intersect: true,
        callbacks: {
          title: (tooltipItems, data) => {
            var xLabel = tooltipItems[0].label
            var label = tooltipItems[0].label
            var dt = moment(xLabel)
            if (this.unit === 'week') {
              let lastday = dt.endOf('isoWeek')
              label = `${moment(xLabel).format('ddd D MMM YYYY')} - ${lastday.format('ddd D MMM YYYY')}`
            } else if (this.unit === 'day') {
              label = `${moment(xLabel).format('ddd D MMM YYYY')}`
            } else if (this.unit === 'hour') {
              let lastday = dt.endOf('hour')
              label = `${moment(xLabel).format('lll')} - ${lastday.format('lll')}`
            } else if (this.unit === 'year') {
              let lastday = dt.endOf('year')
              label = `${moment(xLabel).format('D MMM YYYY')} - ${lastday.format('D MMM YYYY')}`
            }
            return label
          },
          label: (tooltipItem, data) => {
            let datasetIndex = tooltipItem.datasetIndex
            let value = `${moment(tooltipItem.label).format('ddd D MMM')}: ${tooltipItem.value}`
            if (datasetIndex === 1 && this.chartData.datasets[datasetIndex] && this.chartData.metaData.compareLabels) {
              value = `${moment(this.chartData.metaData.compareLabels[moment(tooltipItem.label)]).format('ddd D MMM')}: ${tooltipItem.value}`
            }
            return value
          }
        }
      },
      hover: {
        mode: 'nearest',
        intersect: true
      },
      scales: {
        xAxes: [{
          type: 'time',
          scaleLabel: {
            display: true,
            labelString: 'Time',
            fontColor: '#333'

          },
          time: {
            unit: this.unit,
            isoWeekday: true
          }
        }],
        yAxes: [{
          scaleLabel: {
            display: true,
            lablabelStringel: 'Ratings',
            fontColor: '#333'
          }
        }]
      }
    })
  }
}
